<template>
  <div>
	<Ddtop mes="订单详情"></Ddtop>
	<div class="fenge"></div>

<!-- 订单状态信息 -->
	<div class="ddzhuangtai">
		<div>
			<img src="/img/dingdan/dingdan.png" alt="">
		</div>
		<div class="ztmain">
			<p class="ztmain-top">待付款</p>
			<p class="ztmain-main">请在15分钟内完成支付，否则订单将被取消</p>
			<p style="margin-top: 5px;">剩余时间{{daojishi}}</p>
		</div>
	</div>
	<div class="fenge"></div>

	<!-- 组件 订单信息 -->
	<Ddxinxi></Ddxinxi>

	<!-- 底端取消订单和支付 -->
	<div class="ddzhifu">
		<div @click="quxiaodd()">
			取消订单
		</div>
		<div @click="zhifu()">
			立即支付
		</div>
	</div>
  </div>
</template>

<script>

import Ddtop from "../../components/dingdan-top.vue"
import Ddxinxi from "../../components/ddxinxi.vue"

export default {
  name: 'dingdanxq',
  components: {
	Ddtop,
	Ddxinxi
  },
  data(){
  return{
  time: 900,
  daojishi:""
  }
  },
 methods:{
	// 实现倒计时
    countDown(){
      this.time--;
  var fen = parseInt(this.time/60)
  var miao = this.time%60
  this.daojishi = fen+"分"+miao+"秒"
    },

	// 取消订单
	quxiaodd(){
        alert("取消订单成功！")
		this.$router.push("/home/gouwuche")
		this.$store.state.dingdan = {}
		this.$store.state.shangpinlist = []
		this.$store.state.heji = 0
	},
	// 立即支付
	zhifu(){
		this.$router.push("/querendd")
	}
  },
  mounted(){
    this.time=900;
    setInterval(this.countDown,1000);
  },
  watch:{
    'time':function(newVal){
		// 倒计时结束时 清空订单  返回购物车界面
          if(newVal==0){
			this.$store.state.dingdan = {}
			alert("支付超时，本次订单已失效！")
            this.$router.push('/gouwuche');
          }
    }
  }
}
</script>
<style scoped>
	.fenge{
		height: 20px;
		width: 100%;
		background: #eeeeee;
	}
	.ddzhuangtai{
		height: 153px;
		padding-left: 30px;
		padding-right: 30px;
		width: 100%;
	}
	.ddzhuangtai>div{
		float: left;
	}
	.ddzhuangtai>div:first-child{
		margin-top: 30px;
	}
	.ddzhuangtai .ztmain{
		height: 135px;
		margin-left: 20px;
	}
	.ddzhuangtai .ztmain .ztmain-top{
		height: 55px;
		line-height: 55px;
		color: #84c225;
		font-size: 30px;
	}
	.ddzhuangtai .ztmain>p{
		font-size: 24px;
		color: #515151;
	}


	.ddzhifu{
		height: 100px;
		line-height: 100px;
		width: 100%;
		border-top: 1px solid #eeee;
		background: white;
		position: fixed;
		bottom: 0px;
	}
	.ddzhifu>div{
		float: left;
		width: 50%;
		text-align: center;
		font-size: 30px;
		color: #1b1b1b;
	}
	.ddzhifu>div:last-child{
		background: #ff5510;
		color: white;
	}
</style>
